@import url(https://fonts.googleapis.com/css2?family=Righteous&Roboto&family=Source+Sans+Pro&display=swap);
.search_container{display:flex;justify-content:center;align-items:center;flex-direction:column;margin-top:15rem}.search_container__form{position:relative;width:30rem;background-color:#9edbe7;border:1px solid #07687b;border-radius:10px;font-family:'Roboto', sans-serif}.search_container__form input,.search_container__form button{height:5rem;border:0;font-size:1.8rem;border-radius:10px}.search_container__form input[type="search"]{outline:0;width:100%;padding:0 1rem;-webkit-appearance:none;-moz-appearance:none;appearance:none;-webkit-transition:all 0.4s cubic-bezier(0, 0, 0.43, 1.49);transition:all 0.4s cubic-bezier(0, 0, 0.43, 1.49);-webkit-transition-property:width, border-radius;transition-property:width, border-radius;z-index:1;position:relative}.search_container__form button{display:none;position:absolute;cursor:pointer;top:0;right:0;width:6rem;border-radius:0 10px 10px 0;background-color:#9edbe7;-webkit-transition:all 0.5s cubic-bezier(0, 0, 0.43, 1.49);transition:all 0.5s cubic-bezier(0, 0, 0.43, 1.49);-webkit-transition-property:background-color, color;transition-property:background-color, color;font-size:2.5rem}.search_container__form button:hover{background-color:#07687b;color:#9edbe7}.search_container__form input:not(:placeholder-shown){border-radius:10px 0 0 10px;width:calc(100% - 6rem)}.search_container__form input:not(:placeholder-shown)+button{display:block}.image_list{display:grid;justify-content:center;align-content:center;width:80%;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:0;grid-auto-rows:10px auto;margin:3rem auto}.image_list img{width:200px;margin:0;padding:0}body{background-color:#dfe3e2}.title{position:absolute;width:auto;height:auto;top:50px;left:30px}.title p{font-size:2.3rem;font-family:'Righteous', cursive;color:#849a94;text-shadow:1px 1px rgba(255,255,255,0.8);-webkit-transform:rotate(-10deg);transform:rotate(-10deg)}.title p a{text-decoration:underline;color:black}.title p a:hover{color:#07687b}@media (max-width: 768px){.title{display:none}}

